import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, AbstractControl, FormControl } from '@angular/forms';
import { country } from 'src/app/@core/utils/countries';
export interface SubScription {
  value: string;
  viewValue: string;
}
@Component({
    selector: 'app-signup',
    templateUrl: './signup.component.html',
    styleUrls: ['./signup.component.scss']
})
export class SignupComponent implements OnInit {
  subscription: SubScription[] = [
    {value: 'steak-0', viewValue: 'Silver'},
    {value: 'pizza-1', viewValue: 'Gold'},
    {value: 'tacos-2', viewValue: 'Basic'}
  ];
  code = new FormControl();
  options: string[] = ['One', 'Two', 'Three'];
  contry = country;
  country_code = '';
  hide = true;
  hideC = true;
  selectType = 'Doctor';
  selectTypeStatus: Boolean = false;
  signupText: Boolean = true;
  selectTypeValue = false;
  AfterClickText: Boolean = false;

  RegisterForm: FormGroup;
  constructor(private formBuilder: FormBuilder) { }
  userType: string;
  userTypes: string[] = ['Doctor', 'Patient', 'Pharmacy', 'Agency'];
  ngOnInit() {

    this.RegisterForm = this.formBuilder.group({
      'email': [null, [ Validators.required, Validators.email, Validators.pattern(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/)]],
      'password': [null, [Validators.required, Validators.minLength(6),  Validators.pattern(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,20}$/)]],
      'cpassword': [null, [Validators.required, Validators.minLength(6), Validators.pattern(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,20}$/)]],
      'userType': [null, [Validators.required, Validators.minLength(6)]],
    },
    { validators: this.checkPasswords });

  }

  checkPasswords(RGF: AbstractControl) {
    if (RGF.get('password').value !== RGF.get('cpassword').value) {
      console.log('checkPasswords if' , RGF);

      return RGF.get('cpassword').setErrors({'notmatch': true});
    } else {
      console.log('checkPasswords else' , RGF);

      return RGF.get('cpassword').setErrors(null);
    }

  }

  countryCode(code) {
    console.log('country data ', code);
    this.country_code = code.callingCodes;
    console.log('country data ', this.country_code);
  }

  SubmitRegistrationForm() {
    console.log(this.RegisterForm.value);
  }

  changeradio(event) {
    console.log('changeradio', event.value);
    this.selectType = event.value;
  }

  selectStatusFunction(data) {
    this.selectTypeValue = data;
  }

  selectStatusNextFunction() {
    this.selectTypeStatus = true;
    this.AfterClickText = true;
    this.signupText = false;
  }

  backFunction() {
    this.selectTypeStatus = false;
    this.selectTypeValue = false;
    this.signupText = true;
    this.AfterClickText = false;
  }
}
